.post{
    display: flex;
    align-items: flex-start;
    border-bottom: 1px solid var(--twitter-background);
    padding-bottom: 10px;
}

.post--body{
    padding: 10px;
    flex: 1;
}

.post--body > img{
    border-radius: 20px;
}

.post--footer{
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
}

.post--headerText > h3{
    font-size: 16px !important;
    margin-bottom: 5px;
}

.post--headerDescription{
    margin-bottom: 10px;
    font-size: 15px;
}

.post--badge{
    font-size: 16px !important;
    color: var(--twitter-color);
}

.post--headerSpecial{
    font-weight: 600;
    font-size: 14px;
    color: gray;
}

.post--avatar{
    padding: 10px;
}